table td {
  vertical-align: middle;
}
table td:last-child {
  border-left: 1px solid black;
}

.fraction {
  display: inline-block;
  position: relative;
  top: 10px;
  text-align: center;
  margin-bottom: 10px;
}
.fraction > span {
  display: block;
}
.fraction > span:first-child {
  border-bottom: 1px solid black;
}

.copyright {
  font-variant: small-caps;
  font-size: 0.8em;
}